.intro {
    border: 1px solid var(--border-color);
    border-radius: 1.2rem;
    padding: 0.3rem;
}

.intro__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    gap: 1rem;
}

.intro__bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 33vh;
    border-radius: 1rem;
    position: relative;
    bottom: 0;
    gap: 0.7rem;
}